import React from 'react'
import Link from 'gatsby-link'
import Helmet from 'react-helmet'

import pic11 from '../assets/images/pic11.jpg'

const MultiDevice = (props) => (
    <div>
        <Helmet>
            <title>Multi-device Support</title>
            <meta name="description" content="Multi-device Support" />
        </Helmet>

        <div id="main" className="alt">
            <section id="one">
                <div className="inner">
                    <header className="major">
                        <h1>Multi-device Support</h1>
                    </header>
                    <span className="image main"><img src={pic11} alt="" /></span>
                    <p>The use of mobile devices to surf the web is growing at an astronomical pace, but unfortunately much of the web isn't optimized for those mobile devices. Mobile devices are often constrained by display size and require a different approach to how content is laid out on the screen.</p>
                     
                    <ul className="pagination">
                        <li><Link to="performance"className="button small">Prev</Link></li>
                        <li><Link to="performance" className= "page">1</Link></li>
                        <li><Link to="#" className="page active">2</Link></li>
                        <li><Link to="loading_speeds" className="page">3</Link></li>
                        <li><Link to="base_technologies" className="page">4</Link></li>
                        <li><Link to="clean_design" className="page">5</Link></li>
                        <li><Link to="products" className="page">6</Link></li>
                        <li><Link to="loading_speeds" className="button small">Next</Link></li>
                    </ul>
                </div>
            </section>
        </div>

    </div>
)

export default MultiDevice


